.element {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  align-self: center;
  justify-self: normal;
  background: #fff;
  display: flex;
  flex-direction: column;
}

@media (max-width: 619px) {
  .element {
    margin: 0 auto;
    max-width: 423px;
  }
}
