.elements__list {
  margin: 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(282px, 1fr));
  grid-template-rows: 1fr;
  gap: 20px 17px;
  list-style: none;
}

@media (max-width: 328px) {
  .elements__list {
    grid-template-columns: auto;
  }
}
